<template>
    <div :id="element.uuid">
        <v-container>
            <v-tooltip bottom color="success" :disabled="isTooltip" z-index="10">
                <template v-slot:activator="{ on, attrs }">
                    <v-card outlined :color="minimaptoolbar ? null : colorToolbar" v-bind="attrs" v-on="on">
                        <v-toolbar v-if="!isDatailView" :color=colorToolbar dark hide-on-scroll height="30px" class="drag-handle">
                            <v-hover v-if="minimaptoolbar" v-slot="{ hover }">
                                <v-btn  icon @click="showError">
                                    <v-icon>{{ iselementOpenClose ? (hover? 'mdi-chevron-double-left' :'mdi-chevron-double-right') : (hover? 'mdi-chevron-double-right' :'mdi-chevron-double-left')}}</v-icon>
                                </v-btn>
                            </v-hover>
                            <v-btn v-if="minimaptoolbar" icon @click.stop="dialogPath=true">
                                <v-icon> mdi-routes</v-icon>
                            </v-btn>
                            <dialogPathSetting v-model="dialogPath" :path="element.path" @submit="submitDialog"/>
                            <v-toolbar-title>AP Application Error</v-toolbar-title>
                            <v-spacer></v-spacer>
                            <v-btn v-if="minimaptoolbar" icon @click="viewARXML">
                                <v-icon> mdi-format-text</v-icon>
                            </v-btn>
                        </v-toolbar>
                        <v-toolbar v-else hide-on-scroll dense flat>
                            <v-toolbar-title>AP Application Error</v-toolbar-title>
                        </v-toolbar>
                        <v-card-text v-if="iselementOpenClose">
                            <v-text-field v-model="element.name" :label="'name  <'+element.path +'>'" :rules="rules.name" placeholder="String" style="height: 45px;" class="lable-placeholer-color"
                                        @input='inputErrorName' @focus="clickOtherFields()" outlined dense></v-text-field>
                            <v-text-field v-model="element.desc" label="Desc" @focus="clickOtherFields()" placeholder="String" style="height: 45px;"  outlined dense class="lable-placeholer-color"></v-text-field>
                            <v-text-field v-model="element.errorcode" label="Error Code" @focus="clickOtherFields()" placeholder="int" style="height: 45px;"  outlined dense class="lable-placeholer-color"></v-text-field>
                            <v-select v-model='element.errorDref' label='Error Domain Reference' :items='selErrorDomain' item-text='name' item-value="name" class="lable-placeholer-color"
                                       clearable @click="setErrorDList()" @change="setErrorD()"
                                        @click:clear='clearErrorD()' outlined dense style="height: 45px;">
                                <template v-slot:append-item>
                                    <v-btn outlined color="indigo" dense text small block @click="newErrorD()">
                                        <v-icon >mdi-plus</v-icon>New Item
                                    </v-btn>
                                </template>
                                <template v-slot:prepend>
                                    <v-icon :color="refDomain ? 'red' : ''" @click="setErrorDSelect()">mdi-magnify</v-icon>
                                </template>
                                <template v-slot:append-outer>
                                    <v-icon :id="element.uuid+'/errordomain'"></v-icon>
                                </template>
                            </v-select>
                                <!-- <v-col cols="1">
                                    <v-menu>
                                        <template v-slot:activator="{ on, attrs }">
                                            <v-btn color="deep-purple accent-4" :id="element.uuid+'/errordomain'" dark icon v-bind="attrs" v-on="on" @click="setErrorDList()">
                                                <v-icon>mdi-magnify</v-icon>
                                            </v-btn>
                                        </template>
                                        <v-list>
                                            <v-list-item v-for="(item, i) in selErrorDomain" :key="i" link @click="setErrorD(item)">
                                                <v-list-item-title>{{ item.name }}</v-list-item-title>
                                            </v-list-item>
                                            <v-btn id="newDomainBtn" outlined color="indigo" dense text small block @click="newErrorD()">
                                                <v-icon >mdi-plus</v-icon>New Item
                                            </v-btn>
                                        </v-list>
                                    </v-menu>
                                </v-col>
                                <v-col cols="11">
                                    <v-text-field v-model="element.errorDref" readonly @focus="setErrorDSelect()" clearable @click:clear='clearErrorD()' label="Error Domain Reference" :style="refDomain ? 'height: 43px;border:solid red 2px' : ''" outlined dense class="lable-placeholer-color"></v-text-field>
                                </v-col> -->
                        </v-card-text>
                        <v-card-text v-else>
                            <v-text-field v-model="element.name" :label="'name  <'+element.path +'>'" :rules="rules.name" placeholder="String" style="height: 45px;" class="lable-placeholer-color"
                                        readonly outlined dense></v-text-field>
                        </v-card-text>
                    </v-card>
                </template>
                <span>{{ element.name }}</span>
            </v-tooltip>
            <v-dialog v-model="dialogText" persistent scrollable width="800">
                <v-card >
                    <v-card-title class="text-h6 green accent-1"> Edit Text 
                        <v-spacer></v-spacer>
                        <v-btn @click="saveARXML()" icon color="green darken-1" x-samll dark>
                            <v-icon>mdi-content-save</v-icon>
                        </v-btn>
                        <v-btn @click="cancelARXML()" icon color="green darken-1" x-samll dark>
                            <v-icon>mdi-close</v-icon>
                        </v-btn>
                    </v-card-title>
                    <v-card-text>
                        <br>
                        <v-row>
                            <label style="padding:10px;">&#60;SHORT-NAME&#62;</label>
                            <v-text-field v-model="editARXML.name" placeholder="String" style="height: 15px;" class="lable-placeholer-color" @input='inputErrorName' dense></v-text-field>
                            <label style="padding:10px;">&#60;&#47;SHORT-NAME&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="padding:10px;">&#60;DESC&#62;</label>
                            <v-text-field v-model="editARXML.desc" placeholder="String" style="height: 15px;" class="lable-placeholer-color" dense></v-text-field>
                            <label style="padding:10px;">&#60;&#47;DESC&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="padding:10px;">&#60;ERROR-CODE&#62;</label>
                            <v-text-field v-model="editARXML.errorcode" placeholder="int" style="height: 15px;" class="lable-placeholer-color" dense></v-text-field>
                            <label style="padding:10px;">&#60;&#47;ERROR-CODE&#62;</label>
                        </v-row>
                        <v-row>
                            <label style="padding:10px;">&#60;ERROR-DOMAIN-REF&#62;</label>
                            <v-text-field v-model="editARXML.errorDref" placeholder="Path" style="height: 15px;" class="lable-placeholer-color" dense></v-text-field>
                            <label style="padding:10px;">&#60;&#47;ERROR-DOMAIN-REF&#62;</label>
                        </v-row>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn class="d-inline-flex ml-3 mr-1" color="green darken-1" text  @click="saveARXML()" >
                            Save
                        </v-btn>
                        <v-btn class="d-inline-flex ml-3 mr-1" color="green darken-1" text @click="cancelARXML()">
                            Cancel
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-container>
    </div>
</template>

<script>
import constant from "../store/constants.js"
import { EventBus } from "../main.js"
import dialogPathSetting from '../components/dialogPathSetting.vue'


export default {
    props: ['element', 'isDatailView', 'minimaptoolbar', 'location'],
    components:{dialogPathSetting},
    computed: {
        activeUUID() {
            return this.$store.state.activeUUID
        },
        setting() {
            return this.$store.state.setting
        },
        openElement() {
            return this.$store.state.openElement
        }
    },
    watch: {
        activeUUID(val) {
            this.setToolbarColor(val)
        },
        /*detailViewUUID(val) {
            this.setToolbarColorDetailView(val)
        },*/
        setting(value) {
            this.zoomvalue = value.zoomMain
            if (this.zoomvalue < this.$setZoominTooltip) {
                this.isTooltip = false
            } else {
                this.isTooltip = this.minimaptoolbar
                if (this.zoomvalue  > this.$setZoominLineTitle && this.zoomvalue < this.$setZoominLineSetupStart) {
                    EventBus.$emit('drawLineTitleBar', this.element.uuid, false)
                } else if (this.zoomvalue > this.$setZoominLineSetupStart && this.zoomvalue < this.$setZoominLineSetupEnd) {
                    this.$nextTick(() => {
                        EventBus.$emit('drawLineTitleBar', this.element.uuid, this.iselementOpenClose)
                    })
                }
            }            
        },
    },
    created() {
        this.setToolbarColor(this.$store.state.activeUUID)
    },
    data() {
        return {
            rules: {
                name:  [val => (val || '').length > 0 ],
            },
            colorToolbar: "#6A5ACD",
            zoomvalue: this.$store.state.setting.zoomMain,
            isTooltip: this.minimaptoolbar,
            iselementOpenClose: true,//this.minimaptoolbar, //toolbar만 보여줄것이냐 아니냐 설정 true: 전체 다 보여줌 / false : toolbar만 보여줌
            selErrorDomain: this.$store.getters.getErrorDomain,
            dialogPath: false,
            dialogText: false,
            editARXML: {name:'', desc:'', errorcode: '', errorDref: ''},
            refDomain: false,         
        }
    },
    mounted () {
        if (this.minimaptoolbar && this.zoomvalue < this.$setZoominElement) {
            this.isTooltip = false
        }
        EventBus.$on(this.element.uuid, (refNum) => {
            if (refNum == 1) {
                this.refDomain = true
            } else {
                this.refDomain = false
            }
        })
    },
    methods: {
        submitDialog(element) {
            this.element.path = element
            this.$store.commit('changePathElement', {uuid:this.element.uuid, path: this.element.path, name: this.element.name} )
            this.$store.commit('isintoErrorList', {uuid:this.element.uuid, name:this.element.name, path:this.element.path})
        },
        setToolbarColor(uuid) 
        {
            if(this.element.uuid == uuid) {
                this.colorToolbar = "#FF1493" 
            } else {
                this.colorToolbar = "#6A5ACD"
            }
        },
        setToolbarColorDetailView(isdetail) {
            if(this.element.uuid == isdetail) {
                this.colorToolbar = "#B0E0E6" 
            } else if (this.element.uuid == this.$store.state.activeUUID) {
                this.colorToolbar  = "#FF1493"
            } else {
                this.colorToolbar = "#6A5ACD"
            }
        },
        showError () {
            this.clickOtherFields()
            this.iselementOpenClose = this.iselementOpenClose ? false : true
            this.$nextTick(() => {
                EventBus.$emit('drawLine')
            })
        },
        inputErrorName () {
            this.$store.commit('editError', {compo:"Name", uuid:this.element.uuid, name:this.element.name} )
            this.$store.commit('changePathElement', {uuid:this.element.uuid, path: this.element.path, name: this.element.name} )
            if (this.element.name != '') {
                this.$store.commit('isintoErrorList', {uuid:this.element.uuid, name:this.element.name, path:this.element.path})
            }
        },
        clickOtherFields() {
            if (this.refDomain) {
                this.deleteOpenElement()
                this.refDomain = false
            }
        },

        clearErrorD() {
            var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/errordomain')
            if (endLine != undefined) {
                this.element.errorDref = null
                this.deleteLine(this.element.uuid+'/errordomain')
                this.refDomain = false
            }
            this.$store.commit('isintoErrorList', {uuid:this.element.uuid, clearRef: true})
        },
        setErrorDSelect() {
            console.log('setErrorDSelect')
            if (this.element.errorDref != null) {
                this.refDomain = true
            //if (this.$store.getters.getDeleteOpenElement(this.element.uuid)+1 == this.$store.state.openElement.length) {
                var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/errordomain')
                if (endLine == undefined) {
                    endLine = this.$store.getters.getErrorDomainPath(this.element.errorDref)
                }
                if (endLine != null) {
                    // 기존에 있던거 좌표 바꿔줘야함.
                    if (this.$store.getters.getDeleteOpenElement(endLine) == -1) {
                        this.$store.commit('editErrorDomain', {compo:"drag", uuid: endLine, top: this.element.top + 100, left: this.element.left + this.$setPositionLeft} )
                    }
                    this.$store.commit('setzIndexVisible', {parent:constant.ErrorDomain_str, uuid: endLine, isVisible: true, compo: 'visible', startUUID: this.element.uuid} )
                    this.$nextTick(() => { 
                        EventBus.$emit('new-line', this.element.uuid+'/errordomain', endLine)
                        document.getElementById(endLine+1).scrollIntoView({ behavior: 'smooth', block: 'start' })
                    })
                    this.$store.commit('setViewLineInfo', {start:this.element.uuid+'/errordomain', end:endLine, iscircle:false, refNum:1})
                }
            }
        },
        setErrorDList() {
            this.clickOtherFields()
            this.selErrorDomain = this.$store.getters.getErrorDomain
            this.setactiveUUID()
        },
        setErrorD(){
            var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/errordomain')
            if (endLine != undefined) {
                //기존꺼 삭제해야한다 vuex에서도 삭제하고 mainview에서도 삭제하고 
                this.deleteLine(this.element.uuid+'/errordomain')
            }
            if (this.element.errorDref != null) {
                this.refDomain = true
                endLine = this.$store.getters.getErrorDomainPath(this.element.errorDref)//this.element.errorDref.uuid
                if (this.$store.getters.getDeleteOpenElement(endLine) == -1) {
                    this.$store.commit('editErrorDomain', {compo:"drag", uuid: endLine, top: this.element.top + 100, left: this.element.left + this.$setPositionLeft} )
                }
                this.$store.commit('setzIndexVisible', {parent:constant.ErrorDomain_str, uuid: endLine, isVisible: true, compo: 'visible', startUUID: this.element.uuid} )
                this.$nextTick(() => { 
                    this.newLine(this.element.uuid+'/errordomain', this.element.uuid+'/errordomain', endLine, true)
                    document.getElementById(endLine+1).scrollIntoView({ behavior: 'smooth', block: 'start' })
                    this.$store.commit('isintoErrorList', {uuid:this.element.uuid})
                })
                this.$store.commit('setViewLineInfo', {start:this.element.uuid+'/errordomain', end:endLine, iscircle:false, refNum:1})
            }
            /*console.log(item)
            if( this.element.errorDref != item.name) {
                var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/errordomain')
                if (endLine != undefined && endLine != item.uuid) {
                    //기존꺼 삭제해야한다 vuex에서도 삭제하고 mainview에서도 삭제하고 
                    this.deleteLine(this.element.uuid+'/errordomain')
                }
                //새로 추가해준다
                if (endLine != item.uuid) {
                    this.refDomain = true
                    if (this.$store.getters.getDeleteOpenElement(item.uuid) == -1) {
                        this.$store.commit('editErrorDomain', {compo:"drag", uuid: item.uuid, top: this.element.top + 100, left: this.element.left + this.$setPositionLeft} )
                    }
                    this.$store.commit('setzIndexVisible', {parent:constant.ErrorDomain_str, uuid: item.uuid, isVisible: true, compo: 'visible', startUUID: this.element.uuid} )
                    this.$nextTick(() => { 
                        this.newLine(this.element.uuid+'/errordomain', this.element.uuid+'/errordomain', item.uuid, true)
                        document.getElementById(item.uuid+1).scrollIntoView({ behavior: 'smooth', block: 'start' })
                        this.$store.commit('isintoErrorList', {uuid:this.element.uuid})
                    })
                    this.$store.commit('setViewLineInfo', {start:this.element.uuid+'/errordomain', end:item.uuid, iscircle:false, refNum:1})
                }
                this.element.errorDref = item.name
            } else {
                //if (this.$store.getters.getDeleteOpenElement(this.element.uuid)+1 == this.$store.state.openElement.length) {
                    this.refDomain = true
                    if (this.$store.getters.getDeleteOpenElement(item.uuid) == -1) {
                        this.$store.commit('editErrorDomain', {compo:"drag", uuid: item.uuid, top: this.element.top + 100, left: this.element.left + this.$setPositionLeft} )
                    }
                    this.$store.commit('setzIndexVisible', {parent:constant.ErrorDomain_str, uuid: item.uuid, isVisible: true, compo: 'visible', startUUID: this.element.uuid} )
                    this.$nextTick(() => { 
                        this.newLine(this.element.uuid+'/errordomain', this.element.uuid+'/errordomain', item.uuid, true)
                        document.getElementById(item.uuid+1).scrollIntoView({ behavior: 'smooth', block: 'start' })
                    })
                    this.$store.commit('setViewLineInfo', {start:this.element.uuid+'/errordomain', end:item.uuid, iscircle:false, refNum:1})
                //}
            }*/
            this.setactiveUUID()
        },
        newErrorD() {
            this.$store.commit('addElementErrorDomain', {
                name: this.$store.getters.getNameErrorDomain, path: '', input: false,
                top: this.element.top + 100, left: this.element.left + this.$setPositionLeft, zindex: 10, icon:"mdi-clipboard-outline", validation: false,
                namespace:'', value:'',
            })
            EventBus.$emit('add-element', constant.Service_str)
            EventBus.$emit('add-element', constant.ErrorDomain_str)
            this.$store.commit('editError', {compo:"z", uuid:this.element.uuid, zindex:2} )
            this.selErrorDomain = this.$store.getters.getErrorDomain
        },

        setactiveUUID() {
            this.$store.commit('setuuid', {uuid: this.element.uuid} )
            this.$store.commit('editError', {compo:"z", uuid:this.element.uuid, zindex:10} )
        },
        deleteLine(fineLine) {
            var linenum = this.$store.getters.getconnectLineNum(fineLine)
            if (linenum != -1) {
                this.$store.commit('deletConnectionline', {startnum: linenum} )
                this.deleteOpenElement()
            }
        },
        deleteOpenElement() {
            //EventBus.$emit('delete-line', this.$store.getters.getDeleteOpenElement(this.element.uuid))
            this.$store.commit('deleteOpenElemnt', {uuid: this.element.uuid, isDeleteAll: false, startUUID: this.element.uuid} )
        },
        newLine(startLine, drawLine, endLine, isView) {
            this.$store.commit('setConnectionline', {start: startLine, end: endLine} )
            if (isView) {
                EventBus.$emit('new-line', drawLine, endLine)
            }
        },

        viewARXML() {
            this.editARXML.name = this.element.name
            this.editARXML.desc = this.element.desc
            this.editARXML.errorcode = this.element.errorcode
            this.editARXML.errorDref = this.element.errorDref
            this.dialogText= true
        },
        saveARXML() {
            if (this.element.name != this.editARXML.name) {
                this.$store.commit('editError', {compo:"Name", uuid:this.element.uuid, name:this.editARXML.name} )
                this.$store.commit('changePathElement', {uuid:this.element.uuid, path: this.element.path, name: this.editARXML.name} )
                if (this.editARXML.name != '') {
                    this.$store.commit('isintoErrorList', {uuid:this.element.uuid, name:this.editARXML.name, path:this.element.path})
                }
            }
            this.element.name = this.editARXML.name
            this.element.desc = this.editARXML.desc
            this.element.errorcode = this.editARXML.errorcode
            if (this.element.errorDref != this.editARXML.errorDref) {
                var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/errordomain')
                if (endLine != undefined) {
                    this.deleteLine(this.element.uuid+'/errordomain')
                }
                var changEndLine = this.$store.getters.getErrorDomainPath(this.editARXML.errorDref)
                if (changEndLine != null) {
                    this.newLine(this.element.uuid+'/errordomain', this.element.uuid+'/errordomain', changEndLine, false)
                }
            }
            this.element.errorDref = this.editARXML.errorDref
            this.cancelARXML()
        },
        cancelARXML() {
            this.editARXML = {name:'', desc:'', errorcode: '', errorDref: ''}
            this.dialogText = false
        },
    },

}
</script>